import React, { useEffect, useState } from "react";
import "./App.css";
import Day from "./Day";
import Night from "./Night";
import Button from '@material-ui/core/Button';
import ExpandMoreOutlinedIcon from '@material-ui/icons/ExpandMoreOutlined';
import Header from './components/Header';
import About from './components/About';
import Projects from './components/Projects';
import Skills from './components/Skills';
import Contact from './components/Contact';
import Footer from './components/Footer';
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';
import TrackVisibility from 'react-on-screen';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';


function App() {

  var Scroll   = require('react-scroll');
  var scroller = Scroll.scroller;

  const [theme, setTheme] = useState("light");
  const [showBack, setShowBack] = useState(false);

  function changeTheme() {
    console.log("changing mode")
    let newTheme = theme === "light" ? "dark" : "light";
    setTheme(newTheme);
    localStorage.setItem("stevensprofile", newTheme)
  }

  const checkScroll = () => {
    const position = window.pageYOffset;
    if (position > 100) {
      setShowBack(true);
    } else {
      setShowBack(false);
    }
  };

  useEffect(() => {
    const userPref =localStorage.getItem("stevensprofile");
    console.log(`user pref ${userPref}`)
    if (userPref) {
      setTheme(userPref);
    }
    window.addEventListener("resize", () => {
      window.location.reload();
    });
    window.addEventListener('scroll', checkScroll, { passive: true });
  }, []);

  useEffect(() => {
    theme === "light" ? Day() : Night();
    const userPref =localStorage.getItem("stevensprofile");
    if (userPref) {
      setTheme(userPref);
    }
  }, [theme]);

  function handleScroll(name) {
    scroller.scrollTo(name, {
      duration: 900,
      smooth: true,
      offset: 0, 
    })
  }

  return (
    <div className="App">
      {showBack && <div className="showBack"><div className="back-up" onClick={
        () => {
          scroll.scrollToTop();
        }
      }><ExpandLessIcon fontSize="large"></ExpandLessIcon></div></div>}
      <div id="background">
        <canvas id="bgCanvas"></canvas>
      </div>

      <Header 
        theme={theme}
        changeTheme={changeTheme}
      ></Header>

      <div className="info-container">
        <div className="info-name">Steven Lam</div>

          <div className="expand-down" onClick={() => {handleScroll("About")}}>
            <ExpandMoreOutlinedIcon fontSize="large"></ExpandMoreOutlinedIcon>
          </div>
      </div>

        <Element name="About">
          <TrackVisibility once offset={450}>
            <About theme={theme}></About>
          </TrackVisibility>
        </Element>


      <Element name="Projects">
        <TrackVisibility once offset={450}>
        <Projects theme={theme}></Projects>
        </TrackVisibility>
      </Element>

      <Element name="Skills">
        <TrackVisibility once offset={450}>
        <Skills theme={theme}></Skills>
        </TrackVisibility>
      </Element>

      <Element name="Contact">
        <TrackVisibility once offset={450}>
          <Contact theme={theme}></Contact>
        </TrackVisibility>
      </Element>

      <Footer theme={theme}></Footer>
    </div>
  );
}

export default App;
