import React from 'react';
import './Skills.css';
import styled, { css } from 'styled-components';

const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 800px;
  background-color: ${props => props.theme.lightTheme.background};

  .slide-in {
    transform: translateX(0);
  }

  ${props => props.dark && css`
  background-color: ${props => props.theme.darkTheme.darkerBackground};
  color: white;
  `}
`;

const Title = styled.div `
  margin-top: 35px;
  font-weight: 500;
  font-size: 2.2rem;
  color: gray;
  transform: translateX(-1200px);
  transition: transform 1200ms ease-in;

  :after {
    bottom: -8px;
    content: "";
    display: block;
    height: 4px;
    position: relative;
    width: 60%;
    left: calc(50% - 30%);
    background: gray;
 }

}
`;

const Skills = ({theme, isVisible}) => (
  <Container dark={theme === "dark"}>
     <Title className={isVisible ? 'slide-in' : null}>Skills</Title>
  </Container>
);


export default Skills;
